/**
 *  -  getCachePermissionBytes 
 */

// 
export const safeStorage = {
  // 
  get(key, defaultValue = null) {
    try {
      const data = uni.getStorageSync(key)
      return data !== '' ? data : defaultValue
    } catch (error) {
      console.warn(` [${key}]:`, error)
      return defaultValue
    }
  },

  // 
  set(key, data) {
    try {
      uni.setStorageSync(key, data)
      return true
    } catch (error) {
      console.warn(` [${key}]:`, error)
      return false
    }
  },

  // 
  remove(key) {
    try {
      uni.removeStorageSync(key)
      return true
    } catch (error) {
      console.warn(` [${key}]:`, error)
      return false
    }
  },

  // 
  clear() {
    try {
      uni.clearStorageSync()
      return true
    } catch (error) {
      console.warn(':', error)
      return false
    }
  },

  // 
  getInfo() {
    try {
      return uni.getStorageInfoSync()
    } catch (error) {
      console.warn(':', error)
      return {
        keys: [],
        currentSize: 0,
        limitSize: 0
      }
    }
  }
}

// 
export default safeStorage 